<template>
  <div class="test-analysis">
    <el-page-header @back="goBack" content=""></el-page-header>
    <div class="all-msg">
      <el-table
        :data="msgData"
        style="width: 100%"
        :header-cell-style="{ background: 'rgb(235,244,248)' }"
        :row-style="{ background: 'rgb(240,248,250)' }"
        border
        size="mini"
      >
        <el-table-column
          prop="examName"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="type"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="examTime"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="topScore"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="downScore"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="avgScore"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="allExaminer"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column
          prop="questioner"
          label=""
          align="center"
        ></el-table-column>
      </el-table>
      <p class="target-completion">
        : 1: 0 2: 0 3: 0
      </p>
    </div>
    <div class="bar-chart">
      <div id="bar-chart" :style="{ width: '500px', height: '300px' }"></div>
      <div>
        <p v-for="(value, key, index) in percentList" :key="index">
          {{ key }} : {{ value }}
        </p>
      </div>
    </div>
    <div class="topic">
      <el-table
        :data="topicData"
        style="width: 100%"
        :header-cell-style="{ background: 'rgb(235,244,248)' }"
        :row-style="{ background: 'rgb(240,248,250)' }"
        size="mini"
      >
        <el-table-column
          prop="testId"
          label=""
          align="center"
          width="80"
        ></el-table-column>
        <el-table-column
          prop="topicType"
          label=""
          align="center"
          width="100"
        ></el-table-column>
        <el-table-column
          prop="topicScore"
          label=""
          align="center"
          width="100"
        ></el-table-column>
        <el-table-column
          prop="knowledgePoint"
          label=""
        ></el-table-column>
        <el-table-column
          prop="correctRate"
          label=""
          align="center"
          width="100"
        ></el-table-column>
        <el-table-column label="" width="150" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="checkTopic(scope.row)" type="primary"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
let echarts = require('echarts/lib/echarts');
require('echarts/lib/chart/bar');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
export default {
  data() {
    return {
      examName: null,
      msgData: [
        {
          examName: '',
          type: '',
          examTime: '',
          topScore: '',
          downScore: '',
          avgScore: '',
          allExaminer: null,
          questioner: ''
        }
      ],
      percentList: {
        '60': null,
        '60-70': null,
        '70-80': null,
        '80-90': null,
        '90-100': null
      },
      topicData: [
        {
          testId: 1,
          correctRate: '',
          topicType: '',
          topicScore: '100',
          knowledgePoint: '1, 2, 3'
        }
      ],
      topicMsg: ` 0 +1.`,
      dialogVisible: false,
      scoreChartList: []
    };
  },
  methods: {
    goBack() {
      this.$router.push({ name: 'testMsg' });
    },
    createBarChart() {
      let myChart = echarts.init(document.getElementById('bar-chart'));
      myChart.setOption({
        title: { text: '' },
        tooltip: {},
        xAxis: {
          data: [
            '60',
            '60-70',
            '70-80',
            '80-90',
            '90-100'
          ]
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            data: this.scoreChartList
          }
        ]
      });
    },
    checkTopic(row) {
      this.dialogVisible = true;
      // TODO: 
    },
    getExamScore() {
      this.$ajax
        .get(`/hmu/tea/getGradeByExam/${this.examName}`)
        .then(res => {
          if (!res.result) {
            this.handleScoreData(res.data);
          } else {
            this.$message({
              type: 'error',
              message: res.errMsg
            });
          }
        })
        .catch(err => {
          this.$message({
            type: 'error',
            message: err
          });
        });
    },
    handleScoreData(list) {
      let topScore = 0;
      let downScore = 100;
      let allScore = 0;
      let scoreChartList = [0, 0, 0, 0, 0];
      list.forEach(item => {
        allScore += item.score;
        if (item.score > topScore) {
          topScore = item.score;
        }
        if (item.score < downScore) {
          downScore = item.score;
        }
        if (item.score < 60) {
          scoreChartList[0]++;
        }
        if (item.score >= 60 && item.score < 70) {
          scoreChartList[1]++;
        }
        if (item.score >= 70 && item.score < 80) {
          scoreChartList[2]++;
        }
        if (item.score >= 80 && item.score < 90) {
          scoreChartList[3]++;
        }
        if (item.score >= 90 && item.score <= 100) {
          scoreChartList[4]++;
        }
      });
      this.scoreChartList = scoreChartList;
      this.createBarChart();
      this.msgData[0].allExaminer = list.length;
      this.msgData[0].topScore = topScore;
      this.msgData[0].downScore = downScore;
      this.msgData[0].avgScore = (allScore / list.length).toFixed(2);
      this.msgData[0].examTime = list[0] && list[0].examTime;
      this.msgData[0].examName = list[0] && list[0].examName;
      this.msgData[0].type = this.$route.query.type;
      this.percentList = {
        '60': (scoreChartList[0] / list.length * 100).toFixed(2) + '%',
        '60-70': (scoreChartList[1] / list.length * 100).toFixed(2) + '%',
        '70-80': (scoreChartList[2] / list.length * 100).toFixed(2) + '%',
        '80-90': (scoreChartList[3] / list.length * 100).toFixed(2) + '%',
        '90-100': (scoreChartList[4] / list.length * 100).toFixed(2) + '%'
      };
    }
  },

  mounted() {
    this.examName = this.$route.query.examName;
    this.getExamScore();
  }
};
</script>

<style lang="scss" scoped>
.test-analysis {
  .all-msg {
    margin: 15px;
    text-align: center;
    .target-completion {
      margin: 10px;
    }
  }
  .bar-chart {
    display: flex;
    justify-content: center;
    align-items: center;
    p {
      margin: 20px;
    }
  }
  .el-dialog {
    span {
      display: inline-block;
      width: 100px;
      text-align: center;
    }
  }
}
</style>



